
function convert_yyyymmdd_to_time_Str(yyyymmdd){

    console.log(yyyymmdd);

    var yyyy = yyyymmdd.substring(0, 4);
    var mm = yyyymmdd.substring(4, 6);
    var dd = yyyymmdd.substring(6, 8);

    return yyyy + " " + mm + "  " + dd + "";
}



const outs = { convert_yyyymmdd_to_time_Str }

export default outs